<template>
  <ion-page>
    <ion-header mode="ios">
      <ion-toolbar>
        <ion-title>Ujian</ion-title>
        <ion-back-button slot="start" default-href="/" disabled />
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true" class="exam-page">
      <ion-content class="ion-padding">
        <div v-if="!hasTakenExam">
          <div
            v-if="currentQuestionIndex < questions.length"
            class="question-frame"
          >
            <ion-button
              style="color: white"
              expand="block"
              @click="openQuestionModal"
            >
              Pilih Soal
            </ion-button>
            <ion-card>
              <div class="timer-badge">
                <ion-badge style="color: white">{{
                  formatTime(timer)
                }}</ion-badge>
              </div>
              <br /><br />
              <ion-card-header>
                <ion-card-title>
                  <div v-html="questions[currentQuestionIndex].question"></div>
                </ion-card-title>
              </ion-card-header>
              <ion-card-content>
                <div
                  v-if="questions[currentQuestionIndex].imageUrl"
                  class="media-content"
                >
                  <ion-img
                    :src="questions[currentQuestionIndex].imageUrl"
                  ></ion-img>
                </div>
                <div
                  v-if="questions[currentQuestionIndex].videoUrl"
                  class="media-content"
                >
                  <video
                    :src="questions[currentQuestionIndex].videoUrl"
                    controls
                  ></video>
                </div>
              </ion-card-content>
            </ion-card>
            <div class="options">
              <ion-card
                v-for="(option, key) in questions[currentQuestionIndex].options"
                :key="key"
                @click="selectOption(key)"
                :class="{ selected: answers[currentQuestionIndex] === key }"
              >
                <ion-card-content>{{ option }}</ion-card-content>
              </ion-card>
            </div>
            <ion-button
              style="color: white"
              expand="block"
              @click="nextQuestion"
            >
              {{
                currentQuestionIndex === questions.length - 1
                  ? "Finish"
                  : "Next"
              }}
            </ion-button>
          </div>
          <div v-else>
            <ion-item>
              <ion-label>Nilai Anda: {{ score }}</ion-label>
            </ion-item>
          </div>
        </div>
        <div v-else>
          <ion-item>
            <ion-label>Nilai Anda: {{ score }}</ion-label>
          </ion-item>
        </div>
      </ion-content>
      <ion-toast
        :is-open="toast.isOpen"
        :message="toast.message"
        :duration="toast.duration"
        :color="toast.color"
        @did-dismiss="() => (toast.isOpen = false)"
      ></ion-toast>
      <ion-modal :is-open="isModalOpen" @did-dismiss="closeModal">
        <ion-header>
          <ion-toolbar>
            <ion-title>Pilih Soal</ion-title>
            <ion-buttons slot="end">
              <ion-button @click="closeModal">Tutup</ion-button>
            </ion-buttons>
          </ion-toolbar>
        </ion-header>
        <ion-content>
          <ion-list>
            <ion-item
              v-for="(question, index) in questions"
              :key="index"
              @click="jumpToQuestion(index)"
            >
              <ion-label>Soal {{ index + 1 }}</ion-label>
            </ion-item>
          </ion-list>
        </ion-content>
      </ion-modal>
    </ion-content>
  </ion-page>
</template>

<script setup>
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonContent,
  IonItem,
  IonLabel,
  IonButton,
  IonToast,
  IonBackButton,
  IonBadge,
  IonCard,
  IonCardHeader,
  IonCardTitle,
  IonCardContent,
  IonImg,
  IonModal,
  IonList,
  IonButtons,
} from "@ionic/vue";
import { ref, onMounted, onUnmounted } from "vue";
import {
  getFirestore,
  doc,
  getDoc,
  collection,
  getDocs,
  setDoc,
  serverTimestamp,
} from "firebase/firestore";
import { useRoute, useRouter } from "vue-router";
import { getAuth } from "firebase/auth";
import { firebaseApp } from "../firebase.js";

const db = getFirestore(firebaseApp);
const auth = getAuth(firebaseApp);
const route = useRoute();
const router = useRouter();
const jadwalId = ref(route.params.jadwalId);
const questions = ref([]);
const currentQuestionIndex = ref(0);
const answers = ref([]);
const score = ref(0);
const toast = ref({
  isOpen: false,
  message: "",
  duration: 2000,
  color: "success",
});
const timer = ref(0);
const intervalId = ref(null);
const hasTakenExam = ref(false);
const isModalOpen = ref(false);

const fetchQuestions = async () => {
  const user = auth.currentUser;
  if (user) {
    if (!jadwalId.value) {
      console.error("jadwalId is undefined");
      return;
    }

    try {
      const examResultDoc = await getDoc(
        doc(db, `examResults/${user.uid}/results`, jadwalId.value)
      );
      if (examResultDoc.exists()) {
        hasTakenExam.value = true;
        score.value = examResultDoc.data().score;
        return;
      }

      const jadwalDoc = await getDoc(doc(db, "jadwal", jadwalId.value));
      if (jadwalDoc.exists()) {
        const jadwalData = jadwalDoc.data();
        const questionsSnapshot = await getDocs(collection(db, "soal"));
        questions.value = questionsSnapshot.docs
          .map((doc) => ({
            id: doc.id,
            ...doc.data(),
          }))
          .filter(
            (question) =>
              jadwalData.soal && jadwalData.soal.includes(question.id)
          );
        const savedAnswers = JSON.parse(
          localStorage.getItem(`answers_${jadwalId.value}`)
        );
        if (savedAnswers) {
          answers.value = savedAnswers;
        } else {
          answers.value = new Array(questions.value.length).fill("");
        }
        const savedTime = localStorage.getItem(`timer_${jadwalId.value}`);
        if (savedTime) {
          timer.value = parseInt(savedTime, 10);
        } else {
          timer.value = jadwalData.durasi * 60; // Set timer in seconds
        }
        startTimer();
      } else {
        console.error("jadwalDoc does not exist");
      }
    } catch (error) {
      console.error("Error fetching documents:", error);
    }
  } else {
    console.error("User is not authenticated");
  }
};

const startTimer = () => {
  intervalId.value = setInterval(() => {
    if (timer.value > 0) {
      timer.value--;
      localStorage.setItem(`timer_${jadwalId.value}`, timer.value);
      localStorage.setItem(
        `answers_${jadwalId.value}`,
        JSON.stringify(answers.value)
      );
    } else {
      clearInterval(intervalId.value);
      submitExam();
    }
  }, 1000);
};

const formatTime = (seconds) => {
  const minutes = Math.floor(seconds / 60);
  const secs = seconds % 60;
  return `${minutes.toString().padStart(2, "0")}:${secs
    .toString()
    .padStart(2, "0")}`;
};

const nextQuestion = () => {
  if (currentQuestionIndex.value < questions.value.length - 1) {
    currentQuestionIndex.value++;
  } else {
    calculateScore();
    currentQuestionIndex.value++;
    submitExam(); // Automatically submit exam after last question
  }
};

const calculateScore = () => {
  const correctAnswers = questions.value.reduce((acc, question, index) => {
    if (question.correctAnswer === answers.value[index]) {
      return acc + 1;
    }
    return acc;
  }, 0);
  score.value = Math.round((correctAnswers / questions.value.length) * 100);
};

const selectOption = (option) => {
  answers.value[currentQuestionIndex.value] = option;
  localStorage.setItem(
    `answers_${jadwalId.value}`,
    JSON.stringify(answers.value)
  );
};

const submitExam = async () => {
  const user = auth.currentUser;
  if (user) {
    calculateScore();
    try {
      // Save individual exam result
      await setDoc(doc(db, `examResults/${user.uid}/results`, jadwalId.value), {
        userId: user.uid,
        jadwalId: jadwalId.value,
        score: score.value,
        createdAt: serverTimestamp(),
      });

      // Save summary exam result
      const userDoc = await getDoc(doc(db, "users", user.uid));
      const userData = userDoc.data();

      const jadwalDoc = await getDoc(doc(db, "jadwal", jadwalId.value));
      const jadwalData = jadwalDoc.data();

      const mataPelajaranDoc = await getDoc(
        doc(db, "mataPelajaran", jadwalData.mataPelajaran)
      );
      const mataPelajaranData = mataPelajaranDoc.data();

      const kelasDoc = await getDoc(doc(db, "kelas", jadwalData.kelas));
      const kelasData = kelasDoc.data();

      await setDoc(doc(db, "nilaiUjian", jadwalId.value), {
        userName: userData.fullName,
        kelasName: kelasData.name,
        mataPelajaranName: mataPelajaranData.name,
        score: score.value,
        createdAt: serverTimestamp(),
      });

      showToast("Ujian berhasil disubmit!", "success");
      hasTakenExam.value = true; // Set exam as taken
      localStorage.removeItem(`timer_${jadwalId.value}`);
      localStorage.removeItem(`answers_${jadwalId.value}`);
      router.push({ name: "siswa" });
    } catch (error) {
      console.error("Error submitting exam:", error);
      showToast("Gagal submit ujian.", "danger");
    }
  } else {
    showToast("Pengguna tidak terautentikasi.", "danger");
  }
};

const openQuestionModal = () => {
  isModalOpen.value = true;
};

const closeModal = () => {
  isModalOpen.value = false;
};

const jumpToQuestion = (index) => {
  currentQuestionIndex.value = index;
  closeModal();
};

const showToast = (message, color) => {
  toast.value = {
    isOpen: true,
    message,
    duration: 2000,
    color,
  };
};

onMounted(() => {
  fetchQuestions();
  // Disable browser's back button
  window.history.pushState(null, "", window.location.href);
  window.addEventListener("popstate", preventBack);
});

onUnmounted(() => {
  if (intervalId.value) {
    clearInterval(intervalId.value);
  }
  window.removeEventListener("popstate", preventBack);
});

const preventBack = () => {
  window.history.pushState(null, "", window.location.href);
};
</script>

<style>
.exam-page {
  position: relative;
}

.timer-badge {
  position: fixed;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
}

.question-frame {
  margin-bottom: 20px;
}

.options {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.options ion-card {
  flex: 1 1 calc(50% - 10px);
  cursor: pointer;
  text-align: center;
}

.options ion-card.selected {
  border: 2px solid var(--ion-color-primary);
}
</style>
